.slick-slider {
	position: relative;
	display: block;
	-webkit-touch-callout: none;
	user-select: none;
	touch-action: pan-y;
	-webkit-tap-highlight-color: transparent; }
.slick-list {
	position: relative;
	overflow: hidden;
	display: block;
	height: 100%;
	margin: 0;
	padding: 0;
	cursor: hand;
	&:focus {
		outline: none; }
	.slick-loading & {
		background: white; }
	&.dragging {
		cursor: pointer;
		cursor: hand; } }

.slick-slider .slick-list,
.slick-track,
.slick-slide,
.slick-slide img {
	transform: translate3d(0, 0, 0); }
.slick-track {
	position: relative;
	left: 0;
	top: 0;
	height: 100%;
	display: block;
	zoom: 1;
	&:before,
	&:after {
		content: "";
		display: table; }
	&:after {
		clear: both; }
	.slick-loading & {
		visibility: hidden; } }

.slick-slide {
	float: left;
	height: 100%;
	min-height: 1px;
	img {
		display: block;
		max-width: 100%; }
	&.slick-loading img {
		display: none; }
	&.dragging img {
		pointer-events: none; }
	.slick-initialized & {
		display: block; }
	.slick-loading & {
		visibility: hidden; }
	.slick-vertical & {
		display: block;
		height: auto;
		border: 1px solid transparent; } }

.slick-prev,
.slick-next {
	position: absolute;
	top: 50%;
	margin-top: -10px;
	@include hide-text;
	height: 20px;
	background: none;
	left: 0;
	z-index: 1;
	&:focus {
		outline: none; }
	&.slick-disabled {
		opacity: 0.2; }
	&:before {
		height: 30px;
		line-height: 30px;
		content: "prev";
		display: inline-block;
		vertical-align: middle;
		font-size: 15px;
		color: #fff;
		background: #000; } }
.slick-next {
	right: 0;
	left: auto;
	&:before {
		content: "next";
		display: inline-block; } }
.slick-dots {
	text-align: center;
	margin: 0;
	@include rh(700) {
    position: absolute;
    bottom: -1%;
    right: 0;
    left: 0; }
	li {
		position: relative;
		display: inline-block;
		padding: 0 3px;
		&.slick-active {
			button {
				background: $text-color; } } }
	button {
		display: block;
		width: 10px;
		height: 10px;
		border: 2px solid $text-color;
		background: none;
		cursor: pointer;
		@include hide-text;
		border-radius: 50%; } }
